	module.exports = {"data":{"markdownRemark":{"html":"<p>明日修理されたPCが帰って来るようです。しかしこの故障はかなり痛い……ただでさえギリギリなのに本当に間に合うのかどうか真剣にｶﾞｸﾌﾞﾙだったり。 <a href=\"/emeditor-danmakufu\">EmEditor用東方弾幕風構文ファイル</a>を更新、今回はバグ修正のみです。</p>\n<h2>(拍)danmaqさんまた本スレに来て良いからね</h2>\n<p>ウチにとっての本スレはゲサロでも時封城スレでもなく弾幕風スレなのだ'Ａ｀ 実際、弾幕風スレ以外は殆ど書いてなかったりします。ゲサロは数えるほどしか書いてないし(それもかなり関係ないネタ)、時封城スレに至っては一回も書いてなかったり…なんでこんな伸びるんかなぁ</p>\n<h2>(拍)そのノートPCのスペックが知りたーーー！！！い</h2>\n<p><a href=\"http://www3.toshiba.co.jp/pc/catalog/ss_c/050119lu/lx_spec.htm\">この辺</a>見てもらうとｼｱｰﾜｾかも</p>\n<h2>(拍)ラストスペルの出し方が判らないですー（涙）</h2>\n<p>NORMAL以上の難易度にて全ステージ通してノーミスノーボムが条件です(仮</p>\n<h2>(拍)たまいぬ弁当復活はまだですか</h2>\n<p>直接聞いてはいないのですが、多忙度を見る限り今のところ復活はなさそうな気がします。</p>\n<h2>(拍)度こぞでいろいろ言われてるらしいですがやっぱ、やってる事はすげえと思うので応援してますぜ！！</h2>\n<p>男なら黙って作品を作り上げる、だから美しい。流石に口が多すぎたのは猛省……！</p>\n<h2>(拍)夢終劇体験版面白かったです。製品版の完成を楽しみにしているので、がんばって下さい。</h2>\n<p>（｀・ω・´）</p>\n<h2>(拍)～(　っ　　　　っ ・ω･)</h2>\n<p>./ ,' 3　 `ヽｰっ</p>","frontmatter":{"cover":null,"date":"2006-06-30T20:39:00+09:00","strDate":"2006/6/30","title":"（´・ω・｀）PCあぼーん・その後"}}},"pathContext":{"path":"/ja/blog/2006/06/30-（・ω・｀）pcあぼーん・その後/","langKey":"ja"}}

